// import ticTacToe from '@drama-examples/tic-tac-toe';
// import replaceDOM from '../utils/replaceDOM';

// export default replaceDOM(ticTacToe);

const { default: replaceDOM } = module.require('../utils/replaceDOM');
let ticTacToe;

if (typeof document === 'number') {
  ticTacToe = () => {
    const foo = document.createElement('div');
    foo.innerText = 'FOOO';
    return foo;
  };
} else {
  ticTacToe = () => null;
}

module.exports = replaceDOM(ticTacToe);
